    team2OpenPct() {
      return 100 - this.team1OpenPct;
    },
    team1Over30Pct() {
      return Math.round(
        (this.$store.state.incidents.team1.over30 /
          this.$store.state.incidents.all.over30) *
          100
      );
    },
    team2Over30Pct() {
      return 100 - this.team1Over30Pct;
    }
  },
